@font-face {
  font-family: 'Roboto';
  src: url(./fonts/Roboto.ttf);
}


.show {
  overflow: hidden;
  height: 100%;
}

html {
  height: 100%;
}

body {
  font-family: "Roboto";
  height: 100%;
}

h2 {
  text-transform: uppercase;
}

main {
  height: 100%
}

header {
  display: flex;
  align-items: center;
  background-color: #f4f4f4;
  padding: 10px 25px;
}


.logo > a {
  cursor: pointer;
}



.logo > a > img {
  width: 35px;
  display: inline-block;
  margin-bottom: -2px;
}

.logo > a > h1 {
  display: inline-block;
}

.navbar > ul {
  display: flex;
  list-style: none;
  justify-content: center;
  margin: 16px 0;
}

.navbar > ul > a {
  margin-right: 25px;
  font-size: 20px;
  color: #0077f6;
}

.navbar > ul > a:last-child {
  margin: 0;
}

.navbar > ul > a:focus {
  font-weight: bold;
}

.logo {
  text-align: center;
}

.logo > a > h1 {
  margin: 0;
  font-size: 39px;
  line-height: 57px;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: black;
}

button {
  border: 2px solid #0077f6;
  cursor: pointer;
  color: #0077f6;
  font-size: 18px;
  background: transparent;
  border-radius: 25px;
  margin: 10px 10px;
  padding: 5px 10px;
}

.mainTitle {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: calc(100vh - 104px);
}

.mainTitle > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.mainTitle > div {
  text-align: center;
  z-index: 0
}

.spacer {
  flex: 1;
}

.Works-title {
  padding: 5vw;
}

.Works-title > div > p {
  flex: 1 1 200px;
  margin: 0;
  line-height: 2em;
  opacity: .8;
}

.Works-title > h2 {
  font-size: 3em;
  margin-bottom: 5vh;
}
 
.Works-title > div > nav {
  flex: 1 1 300px;
  align-items: center;
}

.Works-title > div > nav > ul {
  display: flex;
  margin: 0;
  padding: 0;
}

nav > ul > li {
  margin-right: 25px;
  list-style: none;
}

.Works-title > div {
  display: flex;
  flex-wrap: wrap;
}


.Works {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 250px;
  padding: 25px;
  grid-gap: 1em;
  min-height: 100vh;
}

.Work {
  height: 100%;
  box-shadow: 1px 1px 20px 0px #cecece;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 7px;
  transition: box-shadow 0.3s ease-in-out;
  cursor: pointer;
}

.Work > p {
  border: 2px solid #0077f6;
  color: #fff;
  font-size: 18px;
  background-color: #0077f6;
  border-radius: 25px;
  margin: 5px;
  padding: 5px 10px;
}

.Work:hover{
  box-shadow: 1px 1px 3px 0px #dedede;
}

.layout {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #c5ccd68c;
}
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.burger-menu {
  padding: 0;
  display: none;
  flex-direction: column;
  justify-content: space-around;
  height: 30px;
  width: 35px;
  background: transparent;
  border: none;
  cursor: pointer;
  box-sizing: border-box;
  z-index: 222;
}

.burger-menu:focus {
  outline: none;
}

.burger-menu__line {
  width: 30px;
  height: 4.5px;
  background: #257ee4;
  border-radius: 10px;

}

.modul-menu {
  flex-direction: column;
  height: 100%;
  width: 100%;
  background: #f4f4f4;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modul-menu > ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  list-style: none;
  font-size: 20px;
  height: 70%;
  justify-content: space-around;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}

.modul-menu > ul > a {
  cursor: pointer;
}

.modul-menu > div {
  position: absolute;
  bottom: 0px;
  text-align: center;
}

.modul-menu > div > h2 {
  font-size: 15px;
  color: #808080
}

.modul-menu > div > p {
  font-size: 22px;
  color: #131313
}

@media (max-width: 750px) {
  .navbar {
    display: none;
  }
  .burger-menu {
    display: flex;
  }
}





.avatar > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}


.buttonCategories > button:focus {
  font-weight: bolder;
  background-color: #0077f6;
  color: #fff;
}

.Social {
  background: transparent;

}


.Social > ul > li > a > img {
  opacity: .7;
}
.Social > ul > li > a > img:hover {
  opacity: 1
}

.main-text > h1 {
  font-size: 3em;
  color: #fff;
  min-width: 200px;
  max-width: 560px;
  text-align: left;
}

.main-text > span {
  align-items: center;
  display: flex;
  justify-content: flex-end;
  font-size: 22px;
  color: #fff;
  cursor: pointer;
}

.main-text > span:hover {
  font-weight: bold;

}

.main-text > h1 > span {
  padding: 0 10px;
  color: #3799b1;
  background-color: aliceblue;

}

.main-text > div {
  display: flex;
  justify-content: center;
}

.active {
  font-size: 20px;
  font-weight: bolder;
  color:#0089ea
}
@media screen and (max-width: 600px) {
  .main-text > h1 {
    font-size: 2em;
  }
}
