.Project-title {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #4285f4;
    color: aliceblue;
}

.Project-title > div {
    margin: 20px;
}

.Project-title > div > a {
    border: 2px solid aliceblue;
    border-radius: 25px;
    padding: 10px;
    color: aliceblue;
    display: inline-blocks;
    cursor: pointer;
}

.description > h2 {
    margin-bottom: 22px;
    color: rgb(34, 34, 34)
}

.description {
    padding: 5vw;
    color: rgb(105, 105, 105)
}